.product-features {
  color: $color-primary;

  font-weight: 300;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
}

.product-features__subtitle-container {
  padding-top: 53px;
  padding-bottom: 63px;

  @media (min-width: $tablet-width) {
    padding-top: 57px;
    padding-bottom: 47px;

    font-size: 40px;
    line-height: 54px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 77px;
    padding-bottom: 72px;

    font-size: 50px;
    line-height: 68px;
  }
}

.product-features__subtitle:first-child {
  margin: 0 0 40px 0;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}

.product-features__subtitle:last-child {
  margin: 0;
}

.product-features__list {
  width: auto;
  background-color: $color-gray;
  margin: 0;
  padding: 60px 0 325px 0;
  display: grid;
  grid-template-columns: 300px;
  justify-content: center;
  list-style-type: none;
  text-align: center;


  @media (min-width: $tablet-width) {
    padding: 60px 0 0 0;
    grid-template-columns: 268px 350px;

    text-align: left;
  }

  @media (min-width: $desktop-width) {
    padding: 0 0 60px 0;

    grid-template-columns: repeat(3, 250px);
    gap: 68px;

    text-align: center;
  }
}

.product-features__item {
  margin-bottom: 43px;

  @media (min-width: $tablet-width) {
    grid-column: 2/3;
    margin-bottom: 47px;
    padding-left: 40px;
  }

  @media (min-width: $desktop-width) {
    grid-area: auto / auto / auto;
    margin-bottom: 0;
    padding-left: 0;
    padding-top: 360px;
  }
}

.product-features__item:last-child {
  @media (max-width: $mobile-width-only) {
    margin-bottom: 0;
  }
}

.product-features__title {
  margin: 0 0 24px 0;
  padding: 0;

  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  color: #000000;

  @media (min-width: $desktop-width) {
    margin: 0 0 43px 0;

    font-size: 30px;
    line-height: 30px;
  }
}

.product-features__description {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;

  @media (min-width: $desktop-width) {
    margin: 0;

    font-size: 18px;
    line-height: 30px;
  }
}
